import { useState } from "react";

const ChatList = ({ messages }) => {
  // const [prevMsg, setPrevMsg] = useState({});

  let prevMsg = {};

  messages.forEach(msg => {
    prevMsg = msg;
  });

  return (
    <div>
      <ul>
        <style jsx>{`
          div {
            height: 70vh;
          }
          ul {
            height: 70vh;
            width: 90%;
            overflow-y: scroll;
            overflow-x: hidden;
            text-align: right;
            display: flex;
            flex-direction: column;
          }
          li {
            font-size: 1.6rem;
            list-style: none;
            margin: 15px;
          }
          .user {
            border: 1px solid orange;
            border-radius: 15px;
            padding: 12px;
            color: orange;
            text-align: right;
          }
          .other {
            color: lightblue;
            border: 1px solid lightblue;
            border-radius: 15px;
            padding: 12px;
            text-align: left;
          }
          p {
            text-align: left;
            color: white;
            font-size: 1rem;
          }
          .collection {
            margin: 2px;
          }
        `}</style>
        {messages.map(msg => {
          return (
            <li
              className={
                msg.author === localStorage.getItem("username")
                  ? "user " 
                  : "other "
              }
            >
              {msg.dateTime}: {msg.author}- {msg.text}
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default ChatList;
